import React, {useEffect, useRef, useState} from 'react'

export const HorizontalScroll = ({children, className}) => {
    const wrapper = useRef(null);
    const [isBottom, setIsBottom] = useState(false)

    const handleIn = (e) => {
        console.log('in')
        console.log(e.target);
    }

    const handleScroll = (e) => {
        const elem = e.currentTarget;

        if(wrapper.current.getBoundingClientRect().top <= 0) {
           setIsBottom(true);
        }
    }

    const setHandler = () => {
        console.log('isBottom');
    }

    useEffect(() => {
        isBottom && setHandler();
    }, [isBottom])

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)

        return () => {
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])

    return (
        <div ref={wrapper} onScroll={handleScroll} className={`horizontal-scroll ${isBottom ? 'active' : ''}`}>
            <div className="horizontal-scroll__container">
                {children}
            </div>
        </div>
    )
}